import React, { Component } from 'react';
import axios from '../axios';
import { Redirect } from 'react-router-dom'
class SignUp extends Component {
    state = {
    }
    _onSignUp = (e) => {
        this.setState({err: null})
        if (this.state.username && this.state.password && this.state.email) {
            e.preventDefault(),
                axios
                    .post("/api/users", {
                        username: this.state.username,
                        password: this.state.password,
                        avatarUrl: this.state.avatarUrl,
                        email: this.state.email,
                    })
                    .then(response => {
                        this.setState({ usernameLogin: response.data });
                        console.log(response)
                    }

                    )
                    .catch(err => {
                        console.log(err.response.data.errmsg)
                        this.setState({ err: err.response.data.errmsg })
                    }
                    );
        }
    };
    handleChangeText = (index, value) => {
        if (index === 0) this.setState({ username: value })
        else if (index === 1) this.setState({ password: value })
        else if (index === 2) this.setState({ email: value })
        else if (index === 3) this.setState({ avatarUrl: value })
    }
    render() {
        if (this.state.usernameLogin) {
            return <Redirect to='/login' />;
        }
        return (
            <div className="signup">
                <div className="form-signup">
                    <form>
                        <img src="data:image/png;base64," alt="login" />
                        <h1>SignUp</h1>
                        <h4>UserName</h4>
                        <input type="text" required placeholder=" " onChange={e => this.handleChangeText(0, e.target.value)} />
                        <h4>PassWord</h4>
                        <input type="password" required placeholder=" " onChange={e => this.handleChangeText(1, e.target.value)} />
                        <h4>Email</h4>
                        <input type="email" required placeholder=" " onChange={e => this.handleChangeText(2, e.target.value)} />
                        <h4>AvatarUrl</h4>
                        <input className="last" type="text" placeholder=" " onChange={e => this.handleChangeText(3, e.target.value)} />

                        <span style={{ fontWeight: 'bold' }} >Read our Privacy first , if you agree , click Submit ,  <a href='#' style={{ color: 'white' }}> Privacy </a> </span>
                        <button className="btn btn-success btn-block btnLogin" onClick={this._onSignUp} style={{ marginTop: '8px' }}>
                            <span className="spanLogin">
                                Submit
                            </span>
                        </button>
                    </form>
                </div>
                {this.state.err ? (<div id="err"><i className="fas fa-exclamation-triangle">Error!!</i><h4>{this.state.err}</h4></div>) : ''}
            </div>
        )
    }
}


export default SignUp;


